<template>
    <div class="delivery" id="delivery">
        <div class="container__bigmd">
            <div class="delivery__container">
                <div class="delivery__container-title">
                    <h2 class="delivery__title title scrol__animation transition">Доставки</h2>
                </div>
                <div class="delivery__container-content">
                    <div class="delivery__item scrol__animation transition" v-for="item in items" :key="item">
                        <div class="delivery__container-img">
                            <img :src="item.img" alt="Box" class="delivery__img">
                            <h3 class="delivery__title-mini">{{item.title}}</h3>
                            <p><a href="#contacts" class="delivery__text link">{{item.text}}</a></p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<style lang="scss">
    @use "@/assets/styles/default.scss" as s;
    .delivery {
        padding: 60px 0;
        &__container {
            &-content {
                display: flex;
                justify-content: space-between;
                flex-wrap: wrap;

                @include s.media (s.$minilg) {
                    justify-content: space-around;
                }
            }
            &-title {
                display: flex;
                justify-content: center;
                margin-bottom: 60px;
            }
        }
        &__title {
            &-mini {
                padding:5px 0 0 20px;
            }
        }
        &__item {
            border: 1px solid #D8D8D8;
            max-width: 360px;
            width: 100%;

            @include s.media (s.$minilg) {
                margin-bottom: 30px;
            }
        }
        &__text {
            color: #888;
            display: inline-block;
            padding: 10px 20px;
        }
        &__img {
            max-width: 360px;
            width: 100%;
        }
    }
</style>
    
<script>
    export default {
        data: () => ({
            items: [
                {img: require('@/assets/img/main/delivery/box.webp'), title: 'Семейная посылка', text: 'Доставить'},
                {img: require('@/assets/img/main/delivery/compucter.webp'), title: 'Техника', text: 'Доставить'},
                {img: require('@/assets/img/main/delivery/doc.webp'), title: 'Документы', text: 'Доставить'},
            ]
        })
    }
</script>