<template>
    <div class="navbar">
        <div class="container">
            <div class="navbar__container">
                <div class="navbar__container-left">
                    <div class="navbar__logo">
                        <img src="@/assets/img/header/navbar/logo.png" alt="SureDelivery" class="navbar__logo">
                        <h2 class="navbar__logo-text">SUREDELIVERY</h2>
                    </div>
                    <nav class="nav">
                        <ul class="navbar__ul">
                            <li v-for="link in links" :key="link.title">
                                <a :href="link.path" class="navbar__link none">{{ link.title }}</a>
                            </li>
                        </ul>
                    </nav>
                </div>
                <div class="navbar__container-right">
                    <a href="tel:+998770137888" class="navbar__link animation__block none">+998 (77) 013 78 88</a>
                    <transition-group name="fade">
                        <ul class="navbar__buttons" @click="showMobileButton()">
                            <li class="navbar__button"></li>
                            <li class="navbar__button"></li>
                            <li class="navbar__button"></li>
                        </ul>
                    </transition-group>
                </div>
            </div>
        </div>
    </div>
    <nav class="nav__mobile">
        <div class="container__ms">
            <div class="mobile__container">
                <ul class="mobile__ul">
                    <li v-for="link in links" :key="link.title" class="mobile__link">
                        <a :href="link.path" class="navbar__link mobile__link">{{ link.title }}</a>
                        <img class="icon" src="@/assets/img/header/navbar/down.png" alt="Icon">
                    </li>
                    <li class="mobile__link">
                        <a href="tel:+998770137888" class="navbar__link mobile__link">Позвонить</a>
                        <img class="icon" src="@/assets/img/header/navbar/down.png" alt="Icon">
                    </li>
                </ul>
            </div>
        </div>
    </nav>

</template>

<style lang="scss">
@use "@/assets/styles/default.scss" as s;
    .navbar {
        position: fixed;
        top: 0;
        z-index: 999;
        background: #fff;
        width: 100%;
        height: 100px;
        display: flex;
        align-items: center;
        box-shadow: 4px 4px 30px rgba(0, 0, 0, 0.65);
        &__container {
            display: flex;
            justify-content: space-between;
            &-left {
                display: flex;
                justify-content: space-between;
                align-items: center;
                max-width: 1100px;
                width: 100%;
                @include s.media (s.$minilg) {
                    display: block;
                    width: 400px;
                }
                @include s.media (s.$sm) {
                    width: 400px;
                }

                @include s.media (s.$sm) {
                width: 250px;
                font-size: 16px;
            }
            }
            &-right {
                display: flex;
                justify-content: center;
                align-items: center;
            }
        }

        &__logo {
            display: flex;
            align-items: center;
            font-family: montsserat-ExtraBold;
            font-size: 26px;
            color: #E9453B;
            @include s.media (s.$sm) {
                font-size: 20px;
                width: 100px;
            }
            @include s.media (s.$xs) {
                width: 65px;
                font-size: 16px;
            }
        }

        &__link {
            display: block;
            font-family: montsserat-bold;
            font-size: 24px;
            color: #000;
            height: 30px;
            transition: color 500ms;
            position: relative;
            
            &:hover {
                color: #FF0000;
            }

            @include s.media (s.$sm) {
                font-size: 20px;
            }

            @include s.media (s.$sm) {
                font-size: 16px;
            }
        }

        &__ul {
            width: 620px;
            display: flex;
            justify-content: space-between;
        }

        &__buttons {
            height: 40px;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-between;
            display: none;
            @include s.media (s.$minilg) {
                display: flex;
            }
            @include s.media (s.$sm) {
                height: 30px;
            }
            @include s.media (s.$xs) {
                width: 30px;
                font-size: 16px;
            }
        }

        &__button {
            transition: transform 300ms;
            background: #000;
            width: 60px;
            height: 3px;
            position: relative;

            @include s.media (s.$sm) {
                width: 40px;
            }
        }
    }

    .mobile {
        
        &__container {
            max-width: 900px;
            width: 100%;
            height: 400px;
            box-shadow: 0px 15px 20px 0px rgb(0 0 0 / 10%);
            border-bottom-left-radius: 30px;
            border-bottom-right-radius: 30px;
            top: 63px !important;
            padding: 50px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-content: space-between;
            transition: 1000ms;
            transform: translateY(0);
            opacity: 0;
            display: none;
            transform: translateY(10px);
            background: #fff;

            @include s.media (max-width s.$sm) {
                max-width: 770px;
                width: 100%;
            }
        }

        &__link {
            width: 100%;
            height: 60px;
            display: block;
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
    }

    .nav__mobile {
        position: fixed;
        z-index: 999;
        top: 100px;
        left: 100px;
        max-width: 900px;
        width: 100%;
       
    }

    .open {
        transition: transform 300ms;
    }

    .open:nth-of-type(2) {
        display: none;
    }
    .open:nth-of-type(1) {
        transform: rotate(40deg);
        position: absolute;
        top: 55px;
        right: 15px;
    }
    .open:nth-of-type(3) {
        transform: rotate(-40deg);
        position: absolute;
        top: 55px;
        right: 15px;
    }

    .animation__block {
        color: #000 !important;
    }
    .none {
        @include s.media (s.$minilg) {
            display: none;
        }
    }

    .mobile__nav-animation {
        display: block;
        opacity: 100%;
    }

    .icon {
        transform: rotate(-90deg);
    }
</style>

<script>
export default {
    data: () => ({
        docstate: true,
        links: [
            {
                title: 'Услуги',
                path: '#services'
            },
            {
                title: 'Доставки',
                path: '#delivery'
            },
            {
                title: 'Соглашение',
                path: '#agreement'
            },
            {
                title: 'Контакты',
                path: '#contacts'
            },
        ]
    }),

    methods: {
        showMobileButton () {
            let open = document.querySelectorAll('.navbar__button');
            let container = document.querySelector('.mobile__container');
            let body = document.querySelector('body');
            let nav = document.querySelector('.nav__mobile');

            let navbarWidth = nav.clientWidth;
            let documentWidth = body.clientWidth;

            let centerWidth = (documentWidth - navbarWidth) / 2;

            open.forEach(item => item.classList.toggle('open'));
            container.classList.toggle('mobile__nav-animation');

            nav.style.left = `${centerWidth}px`;
        }
    }
}

</script>
